import ResultAnalysis from "../ResultAnalysis/ResultAnalysis";

onmessage = (message) => {
  let args = message.data;
  let printResult = "";
  if (args?.isType === "RESULT_ANALYSIS") {
    printResult = ResultAnalysis(args?.result_analysis);
  } else {
    printResult = "Not Identified Receipt Flow";
  }
  postMessage(printResult);
};
