import jsPDF from "jspdf";
import moment from "moment";
let img = "/images/certificate/logodemo.jpg";
// let studentImg = "/form-testing.jpg";

const ResultAnalysis = (result_analysis) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });
  var width = doc.internal.pageSize.getWidth();
  var firstPageHeight = 0;

  doc.addImage(img, "JPEG", 9, 8, 25, 25);
  doc.addImage(img, "JPEG", width - 34, 7.5, 25, 25);

  //   doc.addImage(result_analysis?.institute.instituteImage, "JPEG", 9, 8, 25, 25);
  //   doc.addImage(result_analysis?.institute.affiliatedImage, "JPEG", width - 34, 7.5, 25, 25);

  doc.setLineWidth(8);
  doc.setDrawColor(255, 255, 255);
  doc.circle(21.4, 20, 16);
  doc.circle(width - 21.4, 20, 16);
  doc.setLineWidth(0);

  firstPageHeight = result_analysis?.institute?.insAffiliated ? 17 : 15;

  doc.setFont("700");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(16);

  const instituteName = doc.splitTextToSize(
    result_analysis?.institute?.insName ??
      "IIT Mumabi Mera by Of Raod of campige sbdas f as",
    146
  );

  doc.text(107, firstPageHeight, instituteName, "center");
  if (instituteName?.length) {
    for (let i = 0; i < instituteName?.length; i++) {
      firstPageHeight += 6;
    }
  }
  if (result_analysis?.institute?.insAffiliated) {
    doc.setFontSize(10);
    doc.text(107, 11, result_analysis?.institute?.insAffiliated, "center");
  }

  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);

  if (result_analysis?.institute.ediatbel1) {
    let editableFirstEdit = doc.splitTextToSize(
      result_analysis?.institute.ediatbel1,
      // edit.first,
      width - 70
    );
    // doc.text(width / 2, firstPageHeight + 5, editableFirstEdit, "center");
    doc.text(width / 2, firstPageHeight, editableFirstEdit, "center");
    if (editableFirstEdit?.length) {
      for (let i = 0; i < editableFirstEdit?.length; i++) {
        firstPageHeight += 5;
      }
    }
  }

  doc.setFont("Arial", "normal", "400");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);
  doc.text(
    width / 2,
    firstPageHeight,
    result_analysis?.institute?.insAddress ??
      "plot no. 77 Adarsh Vihar Buddeshwar Chauraha LKO",
    "center"
  );
  firstPageHeight += 8;

  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);

  doc.setTextColor(0, 0, 0);
  doc.setFontSize(18);
  doc.text(width / 2, firstPageHeight, `Resutl Analysis`, "center");
  firstPageHeight += 12;

  doc.setTextColor("#000000");
  doc.setFontSize(12);

  doc.text(9, firstPageHeight, `Class: `);
  let cls_name = doc.splitTextToSize(
    `${result_analysis?.className ?? "BE"}`,
    width / 3 + 20
  );
  doc.text(20, firstPageHeight, cls_name);

  doc.text(width / 3 + 25, firstPageHeight, `Semester: `);
  let cls_title = doc.splitTextToSize(
    `${result_analysis?.classTitle ?? "VII"}`,
    width / 3 + 10
  );
  doc.text(width / 3 + 42, firstPageHeight, cls_title);
  doc.text(width - 35, firstPageHeight, `Date: `);
  doc.text(width - 25, firstPageHeight, moment().format("DD/MM/yyyy"));

  if (cls_name?.length > cls_title?.length) {
    for (let i = 1; i < cls_name?.length; i++) {
      firstPageHeight += 5;
    }
  } else {
    for (let i = 1; i < cls_title?.length; i++) {
      firstPageHeight += 5;
    }
  }

  firstPageHeight += 10;
  doc.setLineWidth(0.1);
  doc.setDrawColor("#121212");

  doc.line(9, firstPageHeight, width - 9, firstPageHeight);

  return doc.output("bloburl", {
    filename: `result-analysis.pdf`,
  });
};

export default ResultAnalysis;
